/*  SWITCH  */
.switch input {
    display: none;
}

.switch__2 {
    width: 3rem;
    border-radius: 1.6rem;
}

.switch__2 label {
    display: flex;
    align-items: center;
    width: 100%;
    height: 1.5rem;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--clr-greyLight-2),
        -0.2rem -0.2rem 0.5rem var(--clr-white);
    background: rgba(255, 255, 255, 0);
    position: relative;
    cursor: pointer;
    border-radius: 1.6rem;
}

.switch__2 label::after {
    content: "";
    position: absolute;
    left: 0.4rem;
    width: 1.1rem;
    height: 1.1rem;
    border-radius: 50%;
    background: var(--clr-greyLight-1);
    transition: all 0.4s ease;
}

.switch__2 label::before {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background: linear-gradient(
        330deg,
        var(--clr-primary-dark) 0%,
        var(--clr-primary) 50%,
        var(--clr-primary-light) 100%
    );
    opacity: 1;
    transition: all 0.4s ease;
}

.switch input:checked ~ label::before {
    opacity: 1;
}

.switch input:checked ~ label::after {
    left: 56%;
    background: var(--clr-greyLight-1);
}

