/*  PLAY BUTTON  */
.circle {
    width: 9rem;
    height: 100%;
    border-radius: 1rem;
    display: grid;
    justify-items: center;
    align-items: center;
    margin-left: 20px;
}
.circle__btn {
    grid-row: 1/2;
    grid-column: 1/2;
    width: 6rem;
    height: 6rem;
    display: flex;
    margin: 0.6rem;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 3.2rem;
    color: var(--clr-primary);
    z-index: 300;
    background: var(--clr-greyLight-1);
    box-shadow: 0.3rem 0.3rem 0.6rem var(--greyLight-2),
        -0.2rem -0.2rem 0.5rem var(--white);
    cursor: pointer;
    position: relative;
}
.circle__btn.shadow {
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--greyLight-2),
        inset -0.2rem -0.2rem 0.5rem var(--white);
}
.circle__btn .play {
    position: absolute;
    opacity: 0;
    transition: all 0.2s linear;
}
.circle__btn .play.visibility {
    opacity: 1;
}
.circle__btn .pause {
    position: absolute;
    transition: all 0.2s linear;
}
.circle__btn .pause.visibility {
    opacity: 0;
}
.circle__back-1,
.circle__back-2 {
    grid-row: 1/2;
    grid-column: 1/2;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    -webkit-filter: blur(1px);
